import { Options } from "../types";
import QRCodeStyling from "./QRCodeStyling";

const uri = `data:image/png;base64,`;

export default class NightlyQRCode {
  _qrCodeStyling?: QRCodeStyling;

  constructor(address: string, options?: Partial<Options>) {
    this._qrCodeStyling = new QRCodeStyling({
      image: uri,
      data: address,
      width: 188,
      height: 188,
      dotsOptions: {
        color: "#6067F9",
        type: "dist-rounded",
      },
      cornersDotOptions: {
        color: "#6067F9",
        type: "tear",
      },
      cornersSquareOptions: {
        color: "#FFFFFF",
        type: "tear",
      },
      backgroundOptions: {
        color: "#17182B",
      },
      type: "svg",
      qrOptions: {
        errorCorrectionLevel: "Q",
      },
      imageOptions: {
        margin: 4,
        imageSize: 0.4,
      },
      ...options,
    });
  }

  getSvg(): SVGElement | null {
    return this._qrCodeStyling?._svg?.getElement() ?? null;
  }
}
